/* global google */

import React, { useState, useRef ,useEffect} from "react";
import { Row, Col } from "react-bootstrap";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import Form from "react-bootstrap/Form";
import '../lastMile.css';
import { useJsApiLoader, GoogleMap, Autocomplete, Marker, DirectionsRenderer, MarkerF } from '@react-google-maps/api';
import DriverNavbar from "./DriverNavbar";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCirclePlus, faCircleXmark } from '@fortawesome/free-solid-svg-icons';
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function RouteView() {

  const apiUrlRoute = process.env.REACT_APP_API_URL_ROUTE;
  

  const center = { lat: 10.799879466238517, lng: 76.64683924432319 };


  const [showOffcanvas, setShowOffcanvas] = useState(false);

  const toggleOffcanvas = () => setShowOffcanvas(prev => !prev);

  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
    libraries: ['places', 'geometry'],
  });

  const [map, setMap] = useState(null);
  const [distance, setDistance] = useState('');
  const [duration, setDuration] = useState('');
  const [originCoords, setOriginCoords] = useState(null);
  const [destinationCoords, setDestinationCoords] = useState(null);
  const [waypoints, setWaypoints] = useState([]);
  const [polylines, setPolylines] = useState([]);
  const [markers, setMarkers] = useState([]);

  const [directionsResponse, setDirectionsResponse] = useState(null);

  const originRef = useRef();
  const destinationRef = useRef();
  const waypointRefs = useRef([]);

  const addWaypoint = () => {
    setWaypoints((prevWaypoints) => [...prevWaypoints, ""]);
  };

  const removeWaypoint = (index) => {
    setWaypoints((prevWaypoints) => prevWaypoints.filter((_, i) => i !== index));
    waypointRefs.current.splice(index, 1);
  };

  async function getCoordinates(address) {
    const response = await fetch(
      `https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURIComponent(
        address
      )}&key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}`
    );
    const data = await response.json();
    if (data.results && data.results.length > 0) {
      const { lat, lng } = data.results[0].geometry.location;
      return { lat, lng };
    } else {
      throw new Error("Geocoding API returned no results.");
    }
  }


  // ====================calc route ================

  useEffect(() => {
    if (navigator.geolocation) {
      const watchId = navigator.geolocation.watchPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setOriginCoords({ lat: latitude, lng: longitude });
        },
        (error) => console.error("Geolocation error:", error),
        { enableHighAccuracy: true, maximumAge: 10000, timeout: 5000 }
      );
  
      return () => {
        navigator.geolocation.clearWatch(watchId);
      };
    } else {
      console.error("Geolocation is not supported by this browser.");
    }
  }, []);
  
  // Periodically recalculate the route every 10 seconds
  useEffect(() => {
    if (originCoords) {
      const intervalId = setInterval(() => {
        calculateRoute();
      }, 1000); // Recalculate every 10 seconds
  
      return () => clearInterval(intervalId);
    }
  }, [originCoords]);
  
  async function calculateRoute() {
    if (destinationRef.current.value === '') {
      toast.error("Please provide a destination.");
      return;
    }
  
    let routeData;
    try {
      let originCoordinates;
  
      // Use current location or fallback to Google Geolocation API
      if (originRef.current.value === '') {
        try {
          const currentPosition = await new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition(
              (position) => resolve(position.coords),
              (error) => reject(error) // Reject if geolocation fails
            );
          });
          originCoordinates = { lat: currentPosition.latitude, lng: currentPosition.longitude };
        } catch (error) {
          console.warn("Navigator geolocation failed, using Google Geolocation API:", error.message);
  
          // Use Google Geolocation API as a fallback
          const apiKey = process.env.REACT_APP_GOOGLE_MAPS_API_KEY;
          const response = await fetch(`https://www.googleapis.com/geolocation/v1/geolocate?key=${apiKey}`, {
            method: 'POST',
          });
  
          if (!response.ok) {
            throw new Error("Failed to fetch location using Google Geolocation API.");
          }
  
          const data = await response.json();
          originCoordinates = { lat: data.location.lat, lng: data.location.lng };
        }
      } else {
        originCoordinates = await getCoordinates(originRef.current.value);
      }
  
      const destinationCoordinates = await getCoordinates(destinationRef.current.value);
      setOriginCoords(originCoordinates);
      setDestinationCoords(destinationCoordinates);
  
      const waypointCoordinates = waypointRefs.current
        .map((ref) => ref?.value)
        .filter(Boolean);
  
      const formattedWaypoints = waypointCoordinates.map(async (address) => {
        const coordinates = await getCoordinates(address);
        return { latitude: coordinates.lat, longitude: coordinates.lng };
      });
  
      const waypointsData = await Promise.all(formattedWaypoints);
  
      routeData = {
        start_location: { latitude: originCoordinates.lat, longitude: originCoordinates.lng },
        end_location: { latitude: destinationCoordinates.lat, longitude: destinationCoordinates.lng },
        waypoints: waypointsData,
      };
  
      const response = await fetch(`${apiUrlRoute}/routeoptimize/RouteOptimizationView`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(routeData),
      });
  
      const data = await response.json();
      if (!response.ok) {
        throw new Error(`Backend error: ${data.message}`);
      }
  
      // Process and render the optimized route
      const steps = data[0]?.optimized_route?.flatMap((route) => route.steps) || [];
      if (steps.length === 0) {
        throw new Error("No route steps available.");
      }
  
      let fullPath = [];
      steps.forEach((step) => {
        const decodedStepPolyline = google.maps.geometry.encoding.decodePath(step.polyline.encodedPolyline);
        fullPath = fullPath.concat(decodedStepPolyline);
      });
  
      polylines.forEach((polyline) => polyline.setMap(null));
      setPolylines([]);
  
      const newPolyline = new google.maps.Polyline({
        path: fullPath,
        geodesic: true,
        strokeColor: "#FF0000",
        strokeOpacity: 1.0,
        strokeWeight: 4,
      });
      newPolyline.setMap(map);
      setPolylines([...polylines, newPolyline]);
  
      const bounds = new google.maps.LatLngBounds();
      fullPath.forEach((latLng) => bounds.extend(latLng));
      map.fitBounds(bounds);
  
      // Markers and Distance/Durations
      const newMarkers = [
        { position: originCoordinates, label: 'A', tooltipText: 'Current Location' },
        ...waypointsData.map((wp, index) => ({
          position: { lat: wp.latitude, lng: wp.longitude },
          label: String.fromCharCode(66 + index),
          tooltipText: waypointRefs.current[index]?.value || `Waypoint ${index + 1}`,
        })),
        {
          position: destinationCoordinates,
          label: String.fromCharCode(66 + waypointsData.length),
          tooltipText: destinationRef.current.value,
        },
      ];
      setMarkers(newMarkers);
  
      let totalDistance = 0;
      let totalSeconds = 0;
      data[0]?.legs.forEach((leg) => {
        totalDistance += leg.distance;
        totalSeconds += leg.duration.hours * 3600 + leg.duration.minutes * 60 + leg.duration.seconds;
      });
  
      const hours = Math.floor(totalSeconds / 3600);
      const minutes = Math.floor((totalSeconds % 3600) / 60);
      const seconds = totalSeconds % 60;
  
      setDistance(`${(totalDistance / 1000).toFixed(2)} km`);
      setDuration(`${hours}h ${minutes}m ${seconds}s`);
    } catch (error) {
      console.error("Error calculating route:", error);
      toast.error(error.message);
    }
  }
  
  
  // Periodically update the route if traveling
  function startRouteUpdates(intervalMs = 10000) {
    setInterval(async () => {
      await calculateRoute(); // Recalculate route dynamically
    }, intervalMs);
  }
  


  // ===========================

  function clearRoute() {
    polylines.forEach((polyline) => polyline.setMap(null));


    setPolylines([]);
    setMarkers([]);
    setDistance('');
    setDuration('');
    originRef.current.value = '';
    destinationRef.current.value = '';
    waypointRefs.current.forEach((ref) => {
      if (ref) ref.value = '';
    });
    setWaypoints([]);
    if (map) {
      map.panTo(center);
      map.setZoom(15);
    }
  }

  if (!isLoaded) {
    return <div>Loading...</div>;
  }


  

  return (
    <div>
      <DriverNavbar />

      <ToastContainer />

      <div className="mt-5">
        <Row style={{ width: '100%' }}>
          <Col lg={2} className="google-loc-div">
            <Autocomplete>
              <FloatingLabel label="Origin">
                <Form.Control ref={originRef} type="text" placeholder="Origin" className="common-fields" />
              </FloatingLabel>
            </Autocomplete>

            <Autocomplete>
              <FloatingLabel label="Destination">
                <Form.Control ref={destinationRef} type="text" placeholder="Destination" className="common-fields" />
              </FloatingLabel>
            </Autocomplete>

            {waypoints.map((_, index) => (
              <div key={index} className="d-flex align-items-center mb-2">
                <Autocomplete>
                  <FloatingLabel label={`Waypoint ${index + 1}`} className="flex-grow-1">
                    <Form.Control
                      type="text"
                      placeholder={`Waypoint ${index + 1}`}
                      ref={(el) => (waypointRefs.current[index] = el)}
                      className="common-fields"
                    />
                  </FloatingLabel>
                </Autocomplete>
                <FontAwesomeIcon
                  icon={faCircleXmark}
                  className="me-3 text-danger"
                  style={{ cursor: "pointer" }}
                  onClick={() => removeWaypoint(index)}
                />
              </div>
            ))}

            <p className="mt-4 text-start" onClick={addWaypoint}>
              Add Destination <FontAwesomeIcon icon={faCirclePlus} />
            </p>

            <button className="mt-4 btn-common gbtn" onClick={calculateRoute}>
              Calculate Route
            </button>

            <button className="mt-4 btn-common btn-del gbtn" onClick={clearRoute}>
              Clear
            </button>

            <FloatingLabel label="Distance">
              <Form.Control type="text" placeholder="Distance" value={distance} readOnly className="common-fields" />
            </FloatingLabel>

            <FloatingLabel label="Estimated Time">
              <Form.Control type="text" placeholder="Estimated time" value={duration} readOnly className="common-fields" />
            </FloatingLabel>
          </Col>

          <Col lg={10} className="google-map-div">
            <GoogleMap
              center={center}
              zoom={15}
              mapContainerStyle={{ width: '100%', height: '100%' }}
              options={{
                zoomControl: false,
                streetViewControl: false,
                mapTypeControl: false,
                fullscreenControl: false,
              }}
              onLoad={(map) => setMap(map)}
            >
              {markers.map((marker, index) => (
                <MarkerF
                  key={index}
                  position={marker.position}
                  label={marker.label}
                  title={marker.tooltipText}
                />
              ))}
            </GoogleMap>
          </Col>

        </Row>
      </div>
    </div>
  );
}

export default RouteView;
