
/* global google */
import React, { useState, useEffect, useRef } from "react";

import { useNavigate } from 'react-router-dom';
import { Row, Col } from "react-bootstrap";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import Form from "react-bootstrap/Form";
import Container from "react-bootstrap/Container";
import "./lastMile.css"
import Table from "react-bootstrap/Table";
import Select from "react-select";
import { TextField } from "@mui/material";
import LastMileNavbar from "./LastMileNavbar";
import { useJsApiLoader, GoogleMap, Autocomplete, Marker, DirectionsRenderer, MarkerF } from '@react-google-maps/api';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCirclePlus, faCircleXmark, faChevronUp, faChevronDown, faSearch } from '@fortawesome/free-solid-svg-icons';
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import { InputGroup } from "react-bootstrap";


function CustomerList() {

    const navigate = useNavigate()


    const handleOrders = () => {
        navigate('/orderList');
    };



    const apiUrlOrder = process.env.REACT_APP_API_URL_ORDER_CUST;

    const [custData, setCustData] = useState([]);

    const fetchCustData = async () => {
        try {
            const response = await fetch(`${apiUrlOrder}/OrderService/FetchCustomerDataAPI`);
            if (response.ok) {
                const data = await response.json();
                setCustData(data.CustomerData);
            } else {
                console.error('Failed to fetch data');
            }
        } catch (error) {
            console.error('Error:', error);
        }
    };

    useEffect(() => {
        fetchCustData();
    }, []);


    const [selectedCustomer, setSelectedCustomer] = useState(null);

    // Prepare options for the Select dropdown
    const customerOptions = custData.map((cust) => ({
        value: cust.CustomerID,
        label: `${cust.FirstName} ${cust.LastName}`,
    }));

    // Filter the data based on selected customer
    const filteredData = selectedCustomer
        ? custData.filter((cust) => cust.CustomerID === selectedCustomer.value)
        : custData;



    // =================section list=================

    const [selectedSection, setSelectedSection] = useState('customerList');

    // Function to handle section selection
    const showSection = (section) => {
        setSelectedSection(section);
    };


    const [isOpen, setIsOpen] = useState(true); // State to toggle visibility

    const toggleSection = () => {
        setIsOpen((prev) => !prev);
    };


    // ===============map=================

    const center = { lat: 10.799879466238517, lng: 76.64683924432319 };

    const [map, setMap] = useState(null);

    const { isLoaded } = useJsApiLoader({
        googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
        libraries: ['places', 'geometry'],
    });
    async function getCoordinates(address) {
        const response = await fetch(
            `https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURIComponent(
                address
            )}&key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}`
        );
        const data = await response.json();
        if (data.results && data.results.length > 0) {
            const { lat, lng } = data.results[0].geometry.location;
            return { lat, lng };
        } else {
            throw new Error("Geocoding API returned no results.");
        }
    }
    if (!isLoaded) {
        return <div>Loading...</div>;
    }



    return (
        <div >
            <LastMileNavbar />
            <Container >

                <Row className="mt-4">
                    <Col lg={12}>
                        <h6 className="lastmile-heading-main py-2"> Customer List</h6>
                    </Col>
                </Row>


                <ul className="list-group-ul mt-5">

                    <li
                        className={`list-group-item-last ${selectedSection === 'customerList' ? 'active-list-item' : 'inactive-list-item'}`}
                        onClick={() => showSection('customerList')}
                    >
                        Customer List
                    </li>

                    <li
                        className={`list-group-item-last ${selectedSection === 'Address' ? 'active-list-item' : 'inactive-list-item'}`}
                        onClick={() => showSection('Address')}
                    >
                        Address Mapping
                    </li>

                </ul>

                {selectedSection === 'customerList' && (


                    <Row>
                        {/* <Col lg={6} className="mt-2">
                        <h5 className="lastmile-heading py-2"> Add Customers </h5>
                        <Row >
                       
                              
                                    <Col lg={6} className="container-lastmile">
                                        <FloatingLabel
                                            controlId="floatingPassword"
                                            label="Customer Id"
                                            
                                        >
                                            <Form.Control className="common-fields" />
                                        </FloatingLabel>
                                        <FloatingLabel
                                            controlId="floatingPassword"
                                            label="First Name"
                                        >
                                            <Form.Control className="common-fields" />
                                        </FloatingLabel>
    
                                        <FloatingLabel
                                            controlId="floatingPassword"
                                            label="Last Name"
                                        >
                                            <Form.Control className="common-fields" />
                                        </FloatingLabel>
    
    
                                        <FloatingLabel controlId="floatingPassword" label="Address">
                                            <Form.Control className="common-fields" />
                                        </FloatingLabel>
    
                                    </Col >
                                    
                                   <Col lg={6} className="container-lastmile-second">
                                        <FloatingLabel controlId="floatingPassword" label="Email" >
                                            <Form.Control className="common-fields" />
                                        </FloatingLabel>
    
                                        <FloatingLabel controlId="floatingPassword" label="Phone Number ">
                                            <Form.Control className="common-fields" />
                                        </FloatingLabel>
                                        <FloatingLabel controlId="floatingPassword" label="GPS Location">
                                            <Form.Control className="common-fields" />
                                        </FloatingLabel>
    
                                   </Col>
                                   
                            
                                <div>
                                <button  className="mt-2 btn-common ">
                                        Add
                                    </button>{" "}
                                </div>
                        
                        </Row>
                    </Col> */}
                        <Col lg={12} className="mt-2">
                            {/* <h5 className="lastmile-heading py-2">Customer List </h5> */}
                            <Row className="d-flex justify-content-center">
                                <Col lg={2}>
                                    <Select
                                        options={customerOptions}
                                        isSearchable
                                        isClearable
                                        placeholder="Select A Customer"
                                        className="select-box-first"
                                        onChange={(option) => setSelectedCustomer(option)}
                                    />
                                </Col>
                            </Row>

                            <div className="table-responsive-last mt-2" >
                                <Table className="mt-3 ">
                                    <thead className="common-table-thead-last">
                                        <tr >
                                            <th rowSpan={2}>Sl No</th>
                                            <th rowSpan={2}>Customer Id</th>
                                            <th rowSpan={2}>Customer First Name</th>
                                            <th rowSpan={2}>Customer Last Name</th>
                                            <th rowSpan={2}>Address</th>
                                            <th rowSpan={2}>Phone No.</th>
                                            <th rowSpan={2}>Email</th>
                                            <th colSpan={2}>Gps</th>


                                        </tr>
                                        <tr>
                                            <th>Latitude</th>
                                            <th>Longitude</th>
                                        </tr>
                                    </thead>
                                    <tbody>


                                        {filteredData.length > 0 ? (
                                            filteredData.map((cust, index) => (
                                                <tr key={index}>
                                                    <td>{index + 1}</td>
                                                    <td>{cust.CustomerID}</td>
                                                    <td>{cust.FirstName}</td>
                                                    <td>{cust.LastName}</td>
                                                    <td>{cust.Address}</td>
                                                    <td>{cust.PhoneNumber}</td>
                                                    <td>{cust.Email}</td>
                                                    <td>{cust.latitude}</td>
                                                    <td>{cust.longitude}</td>
                                                </tr>
                                            ))
                                        ) : (
                                            <tr>
                                                <td colSpan="9" className="text-center">
                                                    No data available
                                                </td>
                                            </tr>
                                        )}

                                    </tbody>
                                </Table>
                            </div>


                        </Col>
                    </Row>

                )}







            </Container>


            {selectedSection === 'Address' && (
                <>


                    <div  id="btn-hide">
                        <button
                            className="btn-common btn-save mb-3 "
                            onClick={toggleSection}
                            style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            {isOpen ? "Hide Details" : "Show Details"}
                            <FontAwesomeIcon
                                icon={isOpen ? faChevronUp : faChevronDown}
                                className="ms-2"
                            />
                        </button>
                    </div>

                    <Row style={{ width: '100%' }}>





                        <Col lg={2} className="google-loc-div">

                            {/* Toggle button */}

                            {isOpen && (

                                <>

                                    <Row className="row-select-cust">
                                        <Col lg={9} sm={4}>


                                            <Select
                                                isSearchable
                                                isClearable
                                                placeholder="Select A Customer"
                                                className="select-box-first mt-2"

                                            />
                                        </Col>

                                        <Col lg={2} sm={2}>
                                            <button className="mt-4 btn-common btn-save" >
                                                Load
                                            </button>
                                        </Col>
                                    </Row>

                                    

                              
                                        <FloatingLabel label="Customer Id">
                                            <Form.Control type="text" placeholder="Customer Id" readOnly className="common-fields" />
                                        </FloatingLabel>
    
                                        <FloatingLabel label="Customer First Name">
                                            <Form.Control type="text" placeholder="Customer First Name" readOnly className="common-fields" />
                                        </FloatingLabel>
    
                                        <FloatingLabel label="Customer Last Name">
                                            <Form.Control type="text" placeholder="Customer Last Name" readOnly className="common-fields" />
                                        </FloatingLabel>
    
                                        <FloatingLabel label="Phone No.">
                                            <Form.Control type="text" placeholder="Phone No." readOnly className="common-fields" />
                                        </FloatingLabel>
    
                                        <FloatingLabel label="Email">
                                            <Form.Control type="text" placeholder="Email" readOnly className="common-fields" />
                                        </FloatingLabel>
    
                                        <FloatingLabel label="Address">
                                            <Form.Control type="text" placeholder="Address" readOnly className="common-fields" />
                                        </FloatingLabel>
    
                                        <div className="d-flex align-items-center mb-2">
                                            <Autocomplete>
                                                <FloatingLabel label="Search Address" className="flex-grow-1">
                                                    <Form.Control
                                                        type="text"
                                                        placeholder="Enter Address"
    
                                                        className="common-fields"
                                                    />
                                                </FloatingLabel>
                                            </Autocomplete>
                                            <FontAwesomeIcon
                                                icon={faSearch}
    
                                                style={{ cursor: "pointer" }}
    
                                            />
                                        </div>
    
    
    
    
    
    
    
                                        <button className="mt-4 btn-common" >
                                            Add
                                        </button>
    
                                    
                                </>
                            )}

                        </Col>

                        <Col lg={10} className="google-map-div">
                            <GoogleMap
                                center={center}
                                zoom={15}
                                mapContainerStyle={{ width: '100%', height: '100%' }}
                                options={{
                                    zoomControl: false,
                                    streetViewControl: false,
                                    mapTypeControl: false,
                                    fullscreenControl: false,
                                }}
                                onLoad={(map) => setMap(map)}
                            >

                                <MarkerF

                                    position={center}
                                //   label={marker.label}
                                //   title={marker.tooltipText}
                                />

                            </GoogleMap>
                        </Col>

                    </Row>


                </>
            )}
        </div>
    )
}

export default CustomerList