import React, { useState, useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import { Row, Col } from "react-bootstrap";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import Form from "react-bootstrap/Form";
import Container from "react-bootstrap/Container";
import "./lastMile.css"
import Table from "react-bootstrap/Table";
import Select from "react-select";
import { TextField } from "@mui/material";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";



function DriverManagement() {


  const apiUrl = process.env.REACT_APP_API_URL_ORDER_DRIVER;

  const initialFormData = {
    first_name: '',
    last_name: '',
    phn_no: '',
    lic_no: '',
  };

  const [formData, setFormData] = useState(initialFormData);


  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };


  const handleSubmit = async (e) => {
    e.preventDefault();


    const requiredFields = [
      'first_name', 'last_name', 'phn_no', 'lic_no'
    ];

    const emptyFields = requiredFields.filter(field => !formData[field]);


    if (emptyFields.length > 0) {
      // toast.error(`Please fill in the following fields: ${emptyFields.join(', ')}`);
      toast.error(`Please fill in the following fields`);
      return;
    }

    const phoneRegex = /^[0-9]{10}$/;
    if (!phoneRegex.test(formData.phn_no)) {
      toast.error('Please enter a valid 10-digit phone number.');
      return;
    }

    const payload = {
      FirstName: formData.first_name,
      LastName: formData.last_name,
      PhoneNumber: formData.phn_no,
      LicenseNumber: formData.lic_no,
    };

    try {
      const response = await fetch(`${apiUrl}/DriverService/AddDriver`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });

      const data = await response.json();

      if (data.message) {

        setFormData(initialFormData);
        fetchDriverData();

        toast.success(data.message);

      } else {
        toast.error(data.error);
      }
    } catch (error) {
      console.error('Error:', error);
      toast.error(`${error}`);

    }
  };


  const [driveData, setDriveData] = useState([]);

  const fetchDriverData = async () => {
    try {
      const response = await fetch(`${apiUrl}//DriverService/DriverListStatusOne `);
      if (response.ok) {
        const data = await response.json();
        setDriveData(data.driver_list);
      } else {
        console.error('Failed to fetch data');
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  useEffect(() => {
    fetchDriverData();
  }, []);

  // =====================edit=====================

  const [editIndex, setEditIndex] = useState(null);
  const [editFormData, setEditFormData] = useState({});
  const [editErrors, setEditErrors] = useState({});

  // Function to handle input change in the edit form
  const handleEditChange = (e) => {
    const { name, value } = e.target;
    setEditFormData({ ...editFormData, [name]: value });
  };

  const handleEditClick = (index, driver) => {
    setEditIndex(index);
    setEditFormData({ ...driver });
  };

  const handleEditSave = async (e) => {
    e.preventDefault();

    // Initialize an object to store errors
    const errors = {};

    // Validate First Name
    if (!editFormData.FirstName || editFormData.FirstName.trim() === '') {
      errors.FirstName = 'First Name is required';
    }

    // Validate Last Name
    if (!editFormData.LastName || editFormData.LastName.trim() === '') {
      errors.LastName = 'Last Name is required';
    }

    // Validate Phone Number (must be exactly 10 digits)
    const phoneRegex = /^[0-9]{10}$/;
    if (!editFormData.PhoneNumber || !phoneRegex.test(editFormData.PhoneNumber)) {
      errors.PhoneNumber = 'Please enter a valid 10-digit phone number';
    }

    // Validate License Number
    if (!editFormData.LicenseNumber || editFormData.LicenseNumber.trim() === '') {
      errors.LicenseNumber = 'License Number is required';
    }

    // If there are any validation errors, set them and stop the function
    if (Object.keys(errors).length > 0) {
      setEditErrors(errors);
      return;
    }

    // If validation passes, proceed to send the updated data to the server
    try {
      const response = await fetch(`${apiUrl}/DriverService/EditDriver`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          DriverID: editFormData.DriverID,
          FirstName: editFormData.FirstName,
          LastName: editFormData.LastName,
          PhoneNumber: editFormData.PhoneNumber,
          LicenseNumber: editFormData.LicenseNumber,
        }),
      });

      const result = await response.json();

      if (response.status === 200) {
        toast.success(result.message);

        // Update the local data
        const updatedData = [...driveData];
        updatedData[editIndex] = { ...editFormData };
        setDriveData(updatedData);

        // Reset the form and errors
        setEditIndex(null);
        setEditFormData({});
        setEditErrors({});
      } else {
        toast.error(result.error || 'Failed to update data');
      }
    } catch (error) {
      console.error('Error:', error);
      toast.error('An error occurred while updating the data');
    }
  };



  // Handle Cancel button click
  const handleEditCancel = () => {
    setEditIndex(null);
    setEditFormData({});
    setEditErrors({});
  };


  const handleDeleteClick = async (index, driver) => {

    try {

      const response = await fetch(`${apiUrl}/DriverService/DeleteDriver`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ DriverID: driver.DriverID }),
      });

      const result = await response.json();

      if (response.status === 200) {
        toast.success(result.message);

        fetchDriverData()
      } else {
        toast.error(result.error || 'Failed to delete driver');
      }
    } catch (error) {
      toast.error(`An error occurred while deleting the driver ${error}`);
    }
  };

  return (
    <div>


      <Container>

        <form onSubmit={handleSubmit}>
          <Row className="mx-1 d-flex justify-content-center">


            <Col lg={3}>



              <FloatingLabel label="First Name" className="floating-label-custom">
                <Form.Control
                  type="text"
                  name="first_name"
                  placeholder="First Name"

                  className="common-fields common-fields-bug"

                  value={formData.first_name}
                  onChange={handleChange}

                />
              </FloatingLabel>

              <FloatingLabel label="Last Name" className="floating-label-custom">
                <Form.Control
                  type="text"
                  name="last_name"
                  placeholder="Last Name"

                  value={formData.last_name}
                  onChange={handleChange}
                  className="common-fields common-fields-bug"

                />
              </FloatingLabel>


            </Col>



            <Col lg={3}>
              <FloatingLabel label="Phone Number" className="floating-label-custom">
                <Form.Control
                  type="text"
                  name="phn_no"
                  placeholder="Phone Number"
                  value={formData.phn_no}
                  onChange={handleChange}
                  className="common-fields common-fields-bug"
                  isInvalid={formData.phn_no && !/^[0-9]{10}$/.test(formData.phn_no)}
                />
              </FloatingLabel>

              <FloatingLabel label="License  Number" className="floating-label-custom">
                <Form.Control
                  type="text"
                  name="lic_no"
                  placeholder="License  Number"
                  className="common-fields common-fields-bug"
                  value={formData.lic_no}
                  onChange={handleChange}
                />
              </FloatingLabel>



            </Col>

            <Col lg={1}>

              <button className="mt-4 btn-common" type="submit">
                Add
              </button>
            </Col>
            <Col lg={2}></Col>

          </Row>
        </form>




        <div className="table-responsive-last mt-2" >
          <Table className="mt-3 ">
            <thead className="common-table-thead-last">
              <tr >
                <th >Sl No</th>
                <th >Driver Id</th>
                <th >First Name </th>
                <th >Last Name </th>
                <th >Phone Number</th>
                <th >License Number</th>
                <th >Actions</th>
                <th >Delete</th>
              </tr>

            </thead>
            <tbody>

              {driveData.length === 0 ? (
                <tr>
                  <td colSpan="5" className="text-center">No data available.</td>
                </tr>
              ) : (
                driveData.map((driver, index) => (
                  <tr key={driver.DriverID}>
                    <td>{index + 1}</td>
                    <td>{driver.DriverID}</td>

                    <td>
                      {editIndex === index ? (
                        <>
                          <input
                            type="text"
                            name="FirstName"
                            value={editFormData.FirstName}
                            onChange={handleEditChange}
                            className="form-control"
                          />
                          {editErrors.FirstName && (
                            <div className="text-danger">{editErrors.FirstName}</div>
                          )}
                        </>
                      ) : (
                        driver.FirstName
                      )}
                    </td>


                    <td>
                      {editIndex === index ? (
                        <>
                          <input
                            type="text"
                            name="LastName"
                            value={editFormData.LastName}
                            onChange={handleEditChange}
                            className="form-control"
                          />
                          {editErrors.LastName && (
                            <div className="text-danger">{editErrors.LastName}</div>
                          )}
                        </>
                      ) : (
                        driver.LastName
                      )}
                    </td>

                    <td>
                      {editIndex === index ? (
                        <>
                          <input
                            type="text"
                            name="PhoneNumber"
                            value={editFormData.PhoneNumber}
                            onChange={handleEditChange}
                            className="form-control"
                          />
                          {editErrors.PhoneNumber && (
                            <div className="text-danger">{editErrors.PhoneNumber}</div>
                          )}
                        </>
                      ) : (
                        driver.PhoneNumber
                      )}
                    </td>

                    <td>
                      {editIndex === index ? (
                        <>
                          <input
                            type="text"
                            name="LicenseNumber"
                            value={editFormData.LicenseNumber}
                            onChange={handleEditChange}
                            className="form-control"
                          />
                          {editErrors.LicenseNumber && (
                            <div className="text-danger">{editErrors.LicenseNumber}</div>
                          )}
                        </>
                      ) : (
                        driver.LicenseNumber
                      )}
                    </td>

                    <td>
                      {editIndex === index ? (
                        <div className="d-flex">
                          <button className="btn btn-success" onClick={(e) => handleEditSave(e)}>
                            Save
                          </button>
                          <button className="btn btn-danger ms-2" onClick={handleEditCancel}>
                            Cancel
                          </button>
                        </div>
                      ) : (
                        <button className="btn btn-primary" onClick={() => handleEditClick(index, driver)}>
                          Edit
                        </button>
                      )}
                    </td>

                    <td>

                      <button className="btn btn-danger" onClick={() => handleDeleteClick(index, driver)}>
                        Delete
                      </button>
                    </td>
                  </tr>

                ))
              )}

            </tbody>
          </Table>
        </div>
      </Container>





    </div>
  )
}

export default DriverManagement