
import React from "react";
import Container from 'react-bootstrap/Container';
import { useNavigate } from 'react-router-dom';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import '../lastMile.css'
import PurveyLogo from "../../../Assets/PurveyLogo.png";


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCartShopping, faTruckFast, faRoute, faIdCard, faTruck,faRightFromBracket ,faList,faUser} from "@fortawesome/free-solid-svg-icons";

function DriverNavbar() {
    const navigate=useNavigate()
    const handleLogout=()=>{
        navigate('/')
      }
    
      const handleDispatchNote=()=>{
        navigate('/dispatchNote')
      }
     const handleRoute=()=>{
      navigate('/routeView')
     }
     const handleDashboard=()=>{
      navigate('/driverHomePage')
     }

  return (
    <div > 

      <Navbar expand="lg" style={{height:"35px"}} className='common-navbar-last stickynavbar'>
      <Container fluid className='common-navbar-last'>
        <Navbar.Brand ><img src={PurveyLogo} alt="purvey-logo" style={{height:"30px",cursor:"pointer"}}  className="nav-logo-last" onClick={handleDashboard}/></Navbar.Brand>
     
        <Navbar.Toggle aria-controls="basic-navbar-nav-last"/>
        <Navbar.Collapse id="basic-navbar-nav-last">
          
          <Nav className="ms-auto">
            <Nav.Link onClick={handleDispatchNote} className='wms-sidenavbar'>   <FontAwesomeIcon icon={faTruckFast} className="fa-icon-bg-nav" />
            Dispatch Note</Nav.Link>
            <Nav.Link onClick={handleRoute} className='wms-sidenavbar'><FontAwesomeIcon icon={faRoute} className="fa-icon-bg-nav" />  Route View </Nav.Link>
            <Nav.Link onClick={handleLogout} className='wms-sidenavbar'>   <FontAwesomeIcon icon={faRightFromBracket} className="fa-icon-bg-nav-logout" />
            </Nav.Link>
             
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>

  
  </div>
  )
}

export default DriverNavbar