import React, { useState, useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import { Row, Col } from "react-bootstrap";
import { FloatingLabel, Form } from 'react-bootstrap';

import Container from "react-bootstrap/Container";
import "./lastMile.css"
import Table from "react-bootstrap/Table";
import Select from "react-select";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";


function VehicleManagement() {

  const apiUrl = process.env.REACT_APP_API_URL_ORDER_VEHICLE;



  const initialFormData = {
    lic_no: '',
    v_type: '',
    max_load: '',
  };

  const [formData, setFormData] = useState(initialFormData);




  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };


  const validateMaxLoad = (maxLoad) => {
    const loadValue = parseFloat(maxLoad);

    if (isNaN(loadValue) || loadValue <= 0) {
      return false;
    }
    return true;
  };



  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();


    const requiredFields = [
      'lic_no', 'v_type', 'max_load'
    ];

    const emptyFields = requiredFields.filter(field => !formData[field]);


    if (emptyFields.length > 0) {
      // toast.error(`Please fill in the following fields: ${emptyFields.join(', ')}`);
      toast.error(`Please fill in the following fields`);
      return;
    }

    if (!validateMaxLoad(formData.max_load)) {
      toast.error(' Max load capacity must be greater than 0 kg.');
      return;
    }

    const payload = {
      LicensePlateNumber: formData.lic_no,
      VehicleType: formData.v_type,
      MaxLoadCapacity: formData.max_load,
    };

    try {
      const response = await fetch(`${apiUrl}/VehicleService/AddVehicle`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });

      const data = await response.json();

      if (data.message) {

        setFormData(initialFormData);
        fetchVehData();

        toast.success(data.message);

      } else {
        toast.error(data.error);
      }
    } catch (error) {
      console.error('Error:', error);
      toast.error(`${error}`);

    }
  };

  const [vehData, setVehData] = useState([]);

  const fetchVehData = async () => {
    try {
      const response = await fetch(`${apiUrl}/VehicleService/VehicleListStatusOne`);
      if (response.ok) {
        const data = await response.json();
        setVehData(data.vehicle_list);
      } else {
        console.error('Failed to fetch data');
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  useEffect(() => {
    fetchVehData();
  }, []);

// =====================edit=====================

const [editIndex, setEditIndex] = useState(null);
const [editFormData, setEditFormData] = useState({});
const [editErrors, setEditErrors] = useState({});

// Function to handle input change in the edit form
const handleEditChange = (e) => {
  const { name, value } = e.target;
  setEditFormData({ ...editFormData, [name]: value });
};

const handleEditClick = (index, veh) => {
  setEditIndex(index);
  setEditFormData({ ...veh });
};

  const handleEditSave = async (e) => {
    e.preventDefault();
    const errors = {};
    if (!editFormData.LicensePlateNumber.trim()) {
      errors.LicensePlateNumber = 'License Plate Number is required.';
    }
    if (!editFormData.VehicleType.trim()) {
      errors.VehicleType = 'Vehicle Type is required.';
    }
    if (!validateMaxLoad(editFormData.MaxLoadCapacity)) {
      errors.MaxLoadCapacity = 'Max Load Capacity must be greater than 0';
    }

    if (Object.keys(errors).length > 0) {
      setEditErrors(errors);
      return;
    }

    try {
      const response = await fetch(`${apiUrl}/VehicleService/EditVehicle`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          VehicleID: editFormData.VehicleID,
          LicensePlateNumber: editFormData.LicensePlateNumber,
          VehicleType: editFormData.VehicleType,
          MaxLoadCapacity: editFormData.MaxLoadCapacity,
        }),
      });

      const result = await response.json();

      if (response.status === 200) {
        toast.success(result.message);
        const updatedData = [...vehData];
        updatedData[editIndex] = { ...editFormData };
        setVehData(updatedData);
        setEditIndex(null);
        setEditFormData({});
        setEditErrors({});
      } else {
        toast.error(result.error || 'Failed to update vehicle data');
      }
    } catch (error) {
      toast.error(`${error}`);
    }
  };


// Handle Cancel button click
const handleEditCancel = () => {
  setEditIndex(null);
  setEditFormData({});
  setEditErrors({});
};


const handleDeleteClick = async (index,veh) => {
 
  try {
    
    const response = await fetch(`${apiUrl}/VehicleService/DeleteVehicle`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ VehicleID: veh.VehicleID }),
    });

    const result = await response.json();

    if (response.status === 200) {
      toast.success(result.message);

      fetchVehData()
    } else {
      toast.error(result.error || 'Failed to delete vehicle');
    }
  } catch (error) {
    toast.error('An error occurred while deleting the vehicle');
  }
};

// ==================================================
  return (
    <div>


      <Container>
        <form onSubmit={handleSubmit}>
          <Row className="mx-1 d-flex justify-content-center">
            <Col lg={1}></Col>
            <Col lg={3}>



              <FloatingLabel label="License Plate Number" className="floating-label-custom">
                <Form.Control
                  type="text"
                  name="lic_no"
                  placeholder="License Plate Number"

                  className="common-fields common-fields-bug"
                  value={formData.lic_no}
                  onChange={handleChange}
                  required

                />
              </FloatingLabel>

              <FloatingLabel label="Max Load Capacity (kg)" className="floating-label-custom">
                <Form.Control
                  type="number"
                  name="max_load"
                  placeholder="Max Load Capacity (Kg)"

                  className="common-fields common-fields-bug"
                  value={formData.max_load}
                  onChange={handleChange}
                  required


                />
              </FloatingLabel>
            </Col>


            <Col lg={3}>



              {/* Vehicle Type */}
              <FloatingLabel label="Vehicle Type" className="floating-label-custom">
                <Form.Control
                  type="text"
                  name="v_type"
                  placeholder="Vehicle Type"
                  className="common-fields common-fields-bug"
                  value={formData.v_type}
                  onChange={handleChange}
                  required

                />
              </FloatingLabel>

              <button className="mt-4 btn-common me-5">
                Add
              </button>
            </Col>

          </Row>
        </form>
        <div className="table-responsive-last mt-2" >
          <Table className="mt-3 ">
            <thead className="common-table-thead-last">
              <tr >
                <th >Sl No</th>
                <th >Vehicle Id</th>
                <th >License Plate Number</th>
                <th >Vehicle Type</th>
                <th >Max Load Capacity</th>
                <th >Actions</th>
                <th >Delete</th>
              </tr>

            </thead>
         
            <tbody>
        {vehData.length === 0 ? (
          <tr>
            <td colSpan="5" className="text-center">No data available.</td>
          </tr>
        ) : (
          vehData.map((veh, index) => (
            <tr key={veh.VehicleID}>
              <td>{index + 1}</td>
              <td>{veh.VehicleID}</td>

              {/* License Plate Number Field */}
              <td>
                {editIndex === index ? (
                  <>
                    <input
                      type="text"
                      name="LicensePlateNumber"
                      value={editFormData.LicensePlateNumber}
                      onChange={handleEditChange}
                      className="form-control"
                    />
                    {editErrors.LicensePlateNumber && (
                      <div className="text-danger">{editErrors.LicensePlateNumber}</div>
                    )}
                  </>
                ) : (
                  veh.LicensePlateNumber
                )}
              </td>

              {/* Vehicle Type Field */}
              <td>
                {editIndex === index ? (
                  <>
                    <input
                      type="text"
                      name="VehicleType"
                      value={editFormData.VehicleType}
                      onChange={handleEditChange}
                      className="form-control"
                    />
                    {editErrors.VehicleType && (
                      <div className="text-danger">{editErrors.VehicleType}</div>
                    )}
                  </>
                ) : (
                  veh.VehicleType
                )}
              </td>

              {/* Max Load Capacity Field */}
              <td>
                {editIndex === index ? (
                  <>
                    <input
                      type="text"
                      name="MaxLoadCapacity"
                      value={editFormData.MaxLoadCapacity}
                      onChange={handleEditChange}
                      className="form-control"
                    />
                    {editErrors.MaxLoadCapacity && (
                      <div className="text-danger">{editErrors.MaxLoadCapacity}</div>
                    )}
                  </>
                ) : (
                  veh.MaxLoadCapacity
                )}
              </td>

                      <td>
                        {editIndex === index ? (
                          <div className="d-flex">
                            <button className="btn btn-success" onClick={(e) => handleEditSave(e)}>
                              Save
                            </button>
                            <button className="btn btn-danger ms-2" onClick={handleEditCancel}>
                              Cancel
                            </button>
                          </div>
                        ) : (
                          <button className="btn btn-primary" onClick={() => handleEditClick(index, veh)}>
                            Edit
                          </button>
                        )}
                      </td>

                      <td>

                      <button className="btn btn-danger" onClick={() => handleDeleteClick(index, veh)}>
                            Delete
                          </button>
                      </td>
                    </tr>

                  ))
                )}
              </tbody>

           
          </Table>
        </div>

      </Container>




    </div>
  )
}

export default VehicleManagement