import React, { useState, useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import { Row, Col } from "react-bootstrap";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import Form from "react-bootstrap/Form";
import { Modal, Button } from 'react-bootstrap';
import Container from "react-bootstrap/Container";
import "./lastMile.css"
import Table from "react-bootstrap/Table";
import Select from "react-select";
import LastMileNavbar from "./LastMileNavbar";


function OrderService() {



    const orderStatusOptions = [
        { value: 'pending', label: 'Pending' },
        { value: 'processing', label: 'Processing' },
        { value: 'shipped', label: 'Shipped' },
        { value: 'delivered', label: 'Delivered' },
        { value: 'cancelled', label: 'Cancelled' },
        { value: 'returned', label: 'Returned' }
    ];
    const apiUrlOrder = process.env.REACT_APP_API_URL_ORDER_CUST;

    const [orderData, setOrderData] = useState([]);

    const fetchOrderData = async () => {
        try {
            const response = await fetch(`${apiUrlOrder}/OrderService/FetchOrderDataAPI`);
            if (response.ok) {
                const data = await response.json();
                setOrderData(data.OrderData);
            } else {
                console.error('Failed to fetch data');
            }
        } catch (error) {
            console.error('Error:', error);
        }
    };

    useEffect(() => {
        fetchOrderData();
    }, []);




    const [selectedOrder, setSelectedOrder] = useState(null);
    const [showModal, setShowModal] = useState(false);

    const handleViewClick = (order) => {
        setSelectedOrder(order);  // Set the selected order
        setShowModal(true);  // Show the modal
    };

    // Handle Close Modal
    const handleCloseModal = () => {
        setShowModal(false);  // Close the modal
    };

    const formatDate = (dateString) => {
        if (!dateString) return '';
        const date = new Date(dateString);
        const day = String(date.getDate()).padStart(2, '0');
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const year = date.getFullYear();
        return `${day}/${month}/${year}`;
    };
    return (
        <div >
            <LastMileNavbar />
            <Container>

                <Row className="mt-4">
                    <Col lg={12}>
                        <h6 className="lastmile-heading-main py-2"> Order List</h6>
                    </Col>
                </Row>


                <div className="table-responsive-last mt-2" >
                    <Table className="mt-3 ">
                        <thead className="common-table-thead-last">
                            <tr >
                                <th >Sl No</th>
                                <th >Customer First Name</th>
                                <th >Customer Last Name</th>
                                <th >Address</th>
                                <th >Order No.</th>
                                <th >Order Date</th>
                                <th > Number Of Packets To Be Delivered</th>
                                <th >Status</th>
                                {/* <th >View </th> */}

                            </tr>

                        </thead>
                        <tbody>

                            {orderData.map((order, index) => (
                                <tr key={index} >
                                    <td>{index + 1}</td>
                                    <td>{order.CustomerFirstName}</td>
                                    <td>{order.CustomerLastName}</td>
                                    <td>{order.Address}</td>
                                    <td>{order.OrderNo}</td>
                                    <td>{formatDate(order.OrderDate)}</td>
                                    <td>{order.PacketsToBeDelivered}</td>
                                    <td>{order.OrderStatus}</td>
                                    {/* <td>
                                        <button className="btn-common" onClick={() => handleViewClick(order)}>View</button>
                                    </td> */}
                                </tr>
                            ))}



                        </tbody>
                    </Table>



                    {/* {selectedOrder && (
                        <Modal show={showModal} onHide={handleCloseModal} size="lg">
                            <Modal.Header closeButton>
                                <Modal.Title>
                                    Order Details of Order No - {selectedOrder.OrderNo} - Customer Name: {selectedOrder.CustomerFirstName} {selectedOrder.CustomerLastName}
                                </Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <div className="table-responsive-last mt-2" >
                                    <Table className="mt-3 ">
                                        <thead className="common-table-thead-last">
                                            <tr >
                                                <th >Sl No</th>
                                                <th >Pack Type</th>
                                                <th >Product Name</th>
                                                <th >Product Quantity</th>
                                            </tr>

                                        </thead>
                                        <tbody>
                                         
                                              
                                                    <tr >
                                                        <td> 1</td>
                                                        <td>{selectedOrder.carton_det}</td>
                                                        <td>{selectedOrder.productname}</td>
                                                        <td>{selectedOrder.product_qty}</td>
                                            </tr>

                                        </tbody>
                                    </Table>
                                </div>

                            </Modal.Body>

                        </Modal>
                    )} */}



                </div>



                {/* <Row className="mx-1">

                    <Col lg={2} className="container-lastmile">

                        <Select
                            className="common-select-last my-2"
                            isSearchable
                            isClearable
                            placeholder="Select A Customer"
                        />
                        <Select
                            className="common-select-last my-2"
                            options={orderStatusOptions} // Set the order status options here
                            isSearchable
                            isClearable
                            placeholder="Order Status"
                        />


                        <Select
                            className="common-select-last my-2"

                            isSearchable
                            isClearable
                            placeholder="Assigned Vehicle Id"
                        />

                        <Select
                            className="common-select-last my-2"

                            isSearchable
                            isClearable
                            placeholder="Assigned Driver Id"
                        />



                    </Col>

                    <Col lg={1}></Col>

                    <Col lg={3}>
                        <TextField
                            variant="standard"
                            label="Delivery Address"


                            fullWidth
                            type="text"
                            className="my-2"
                        />

                        <TextField
                            variant="standard"
                            label="Delivery Window Start"
                            focused

                            fullWidth
                            type="datetime-local"
                            className="my-2"
                        />
                        <TextField
                            variant="standard"
                            label="Delivery Window End"
                            focused
                            className="my-2"
                            fullWidth
                            type="datetime-local"
                        />

                    </Col>



                    <Col lg={1}></Col>

                    <Col lg={3}>

                        <TextField
                            variant="standard"
                            label="Distance to customer (Km)"

                            className="my-2"
                            fullWidth

                        />
                        <TextField
                            variant="standard"
                            label="Package Weight (g)"


                            fullWidth

                        />
                        <button className="mt-4 btn-common ">
                            Add
                        </button>
                    </Col>

                </Row>



               
                <div className="table-responsive mt-2" >
                    <Table className="mt-3 ">
                        <thead className="bg-secondary size-sm">
                            <tr className="common-table-thead">
                                <th >Sl No</th>
                                <th >Customer ID</th>
                                <th >Delivery Window Start</th>
                                <th >Delivery Window End</th>
                                <th >Order Status</th>
                                <th >Vehicle Id</th>
                                <th >Driver Id</th>
                                <th >Distance To Customer(Km)</th>
                                <th >Package Weight(g)</th>
                            </tr>
                          
                        </thead>
                        <tbody>
                           


                        </tbody>
                    </Table>
                </div> */}

            </Container >
        </div >
    )
}






export default OrderService